.ChildsList {
 width: 100%;
}

.ChildsList ul {
    margin:auto;
    width: 100%;
}

.ChildsList li {
    display: -ms-flexbox;
    display: flex;
    width: 70%;
    margin: auto;
}

.Delete {
    display: block;
}

.Name {
    font-size: 1.2rem;
    -ms-flex: 1 1;
        flex: 1 1
}

/* TABLET && LARGE SMARTPHONES */
@media screen and (min-width: 481px ) and (max-width: 768px) {
    .ChildsList ul {
        margin:auto;
        width: 80%;
    }
}
/* LAPTOPS && TABLET in Landscape */
@media screen and (min-width: 769px ) and (max-width: 1024px) {
    .ChildsList ul {
        margin:auto;
        width: 80%;
    }
}
/* LARGE DESKTOPS */
@media screen and (min-width: 1025px ) and (max-width: 1600px) {
    .ChildsList ul {
        margin:auto;
        width: 70%;
    }

}


